import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import FavoriteTwoToneIcon from '@material-ui/icons/FavoriteTwoTone';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
}));

const data =
{
  'BTC': {
    logo: '/logo-btc.png',
    address: '1NS1gD6BjM2zud7W9sVvDsesoq6NfNpqrb',
    network: 'Bitcoin (BTC)',
    qr: '/qr-btc.png',
  },
  'ETH / PAXG': {
    logo: '/logo-eth.png',
    address: '0x03a03ad6123d85d3015b5d6bc2064e73acbff85b',
    network: 'Ethereum (ETH / ERC20)',
    qr: '/qr-eth.png',
  },
  'BNB / BUSD / USDT / ETH': {
    logo: '/logo-bnb.png',
    address: '0x03a03ad6123d85d3015b5d6bc2064e73acbff85b',
    network: 'Binance Smart Chain (BEP20)',
    qr: '/qr-bnb.png',
  },
};

const Deposit = (props) => {
  const { type } = props;
  const classes = useStyles();

  return (
    <Grid item xs={12}>
      <Paper style={{ height: '160px', minWidth: '750px', maxWidth: '1000px' }} className={classes.paper}>
        <Grid container direction="row" justify="space-between" alignItems="center">
          <Grid item xs={3}>
            <img src={data[type].logo} alt={"logo-" + type} height="120px" />
          </Grid>
          <Grid item xs={6}>
            <p>
              <b>{type}</b> deposit address:
            </p>
            <p>
              <b>{data[type].address}</b>
            </p>
            <p>
              Network Name: <b>{data[type].network}</b>
            </p>
          </Grid>
          <Grid item xs={3}>
            <img src={data[type].qr} alt={"QR-" + type} />
          </Grid>
        </Grid>
      </Paper>
    </Grid>
  )
}

const Support = (props) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container item spacing={3} direction="column" justify="flex-start" alignItems="flex-start">
        <Grid item xs={12}>
          <Typography variant="h3" noWrap>
            Support Us
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Paper style={{ minWidth: '750px', maxWidth: '1000px' }} className={classes.paper}>
            <p>
              We hope that you're enjoying Balance Bot. The Balance Bot team develops the application in their spare time, and they're not getting paid for the work they do. Their wish is to <b>keep Balance Bot for free forever</b>. And you can help.
            </p>
            <p>
              If you're an enthusiastic Balance Bot user, please considering contributing through the options below. Thank you so much! <FavoriteTwoToneIcon fontSize="small" color="secondary" />
            </p>
            <p>
              The deposit addresses below are all on Binance, which makes it possible to transfer coins <b>without transaction fees</b> from your own Binance account. For more information, see <Link href="https://www.binance.com/en/support/faq/360037037312" target="_blank" rel="noopener" color="secondary">here</Link>. Note: Binance does show a transaction fee, but it will not be charged.
            </p>
            <p>
              Donations are anonymous, but feel free to let us know about your generosity and <Link href="https://t.me/hodlerhacks" target="_blank" rel="noopener" color="secondary">send us a message</Link>.
            </p>
          </Paper>
        </Grid>
        <Deposit type="BTC" />
        <Deposit type="ETH / PAXG" />
        <Deposit type="BNB / BUSD / USDT / ETH" />
      </Grid>
    </div>
  );
}
export default Support;
