import React, { useContext, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TimelineView from "../components/TimelineView";
import StatusView from "../components/StatusView";
import LineChartCoins from '../components/LineChartCoins';
import { BalancesContext } from '../context/BalancesContext';
import { PerformanceContext } from '../context/PerformanceContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Hidden from '@material-ui/core/Hidden';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const CoinAnalysis = () => {
  const classes = useStyles();

  const balances = useContext(BalancesContext)[0];
  const performance = useContext(PerformanceContext)[0];
  const configuration = useContext(ConfigurationContext)[0];
  const [open, setOpen] = useState(true);

  useEffect(() => {
    setOpen(Object.keys(balances).length === 0);
    return () => {
    }
  }, [balances]);

  return (
    <div className={classes.root}>
      <Backdrop className={classes.backdrop} open={open}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4} lg={6}>
          <Typography variant="h3" noWrap>
            Coin Analysis
          </Typography>
        </Grid>
        <Hidden xsDown>
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <TimelineView data={performance} />
          </Grid>
        </Hidden>
        <Grid item xs={12} sm={6} md={4} lg={3}>
          <StatusView />
        </Grid>
        <Grid item xs={12}>
          <Paper style={{ height: '700px' }} className={classes.paper}>
            <LineChartCoins performance={performance} balances={balances} title={`Coin ${configuration.basecoin} Price Changes (%)`} />
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}

export default CoinAnalysis;